#portfolio {
    background-color: #181c24;
    padding-top: 3vmin;
    padding-bottom: 10vmin;
}

.projects-container {
    display: flex;
    flex-wrap: wrap;
    margin-left: 2em;
    margin-right: 2em;
}

@media (max-width: 200px) {
    .flex-container {
        flex-direction: column;
    }
}

.project-card {
    flex-grow: 1;
    flex-basis: 300px;
    background-color: #0c0d12;
    margin: 1em;
}

.project-image-container {
    background: yellow;
    height: 9em;
    width: 16em;
    margin: auto;
}

.card-description {
    padding: 1.5em;   
}

